import DeckTest from '../components/deckGl/test'
import HexagonLayerCom from "../components/deckGl/hexagonLayer";
import Camera1 from "../components/resium/camera"
import TripLayer from "../components/deckGl/tripsLayer";
import React from "react"

const deckRoutes =  {
    deckGL : [
        {
            path: '/deckGL/lineLayer',
            name: 'lineLayer',
            component : (data) => <DeckTest {...data} />
        },
        {
            path: '/deckGL/hexagonLayer',
            name: 'hexagonLayer',


            // eslint-disable-next-line react/react-in-jsx-scope
            component : () => <HexagonLayerCom/>
        },
        {
            path: '/deckGL/tripsLayer',
            name: 'tripsLayer',


            // eslint-disable-next-line react/react-in-jsx-scope
            component :() => <TripLayer />
        },

    ],
    resium : [
        {
            path: '/resium/basic',
            name: 'basic',
            component : () => <Camera1/>
        },
        {
            path: '/resium/2',
            name: '2',


            // eslint-disable-next-line react/react-in-jsx-scope
          //  component : () => <HexagonLayerCom/>
        },

    ],

}

export default deckRoutes


