import React, {Component} from "react";
import deckGlRoutes from "../routes/deckRoute"
import Left from "../components/layouts/left";
import axios from 'axios'
import moment from "moment";

/*
* Container     React  HOF  ?
* -> Container pattern   
*
*
* */

class PageContainer extends Component{
    constructor() {
        super();
        this.state={
            pathData: null,

        }
    }






    componentDidMount() {
        axios.get('http://localhost:80/hi')
            .then( (response)=> {
//                 // handle success
                console.log(response.data);
                const tripsData = response.data.reduce((acc, cur) => {
                    const obj = acc[cur.bikeNo] || {
                        timestamps: [],
                        path:[]
                    }
                    return(
                        {
                            ...acc,
                            [cur.bikeNo] : {
                                timestamps: [
                                    ...obj.timestamps,
                                    Number(moment(cur.rentalDate, 'YYYYMMDDHHmm').toDate().getTime() / 60000000)
                                ],
                                path : [
                                    ...obj.path,
                                    [
                                        cur.rentalSpot.lon,
                                        cur.rentalSpot.lat,
                                    ]
                                ]
                            }}
                    )
                }, {})
                const trips = Object.keys(tripsData).reduce((acc,cur)=>{
                    return [
                        ...acc,
                        {
                            vendor :cur,
                            ...tripsData[cur]
                        }
                    ]
                },[])
                console.log(trips)
                this.setState({
                    pathData: trips,
                })

// //
            })
            }




    render() {
        const {params, path} = this.props.match

        const key = path.split('/')[1]
        const routes = deckGlRoutes[key]
        const findComponent = routes.find(route =>route.name===params.param)
        //console.log(findComponent.component())
        return  (
            <div className="gallery-wrapper">
                <div className="fullheight">
                    <div className="flexbox--row">
                        <div className="flexbox-item" style={{zIndex: 1}}>
                            <Left routes={routes}/>
                        </div>
                        <div className={'flexbox-item flexbox-item--fill'}>
                            {/*<HexagonLayerCom2/>*/}
                            {findComponent && findComponent.component(this.state)}

                        </div>
                    </div>
                </div>
            </div>


        )
    }

}

export default PageContainer
