import React, {Component} from "react";
import DeckGL from "@deck.gl/react";
import {StaticMap} from "react-map-gl";
import {TripsLayer, } from '@deck.gl/geo-layers';
import {AmbientLight, PointLight, LightingEffect} from '@deck.gl/core';
import axios from "axios";
import moment from "moment";
import {GeoJsonLayer, LineLayer, PathLayer} from "@deck.gl/layers";


const MAPBOX_ACCESS_TOKEN  = 'pk.eyJ1IjoiZmVucmlyZCIsImEiOiJjazkyYmQyNDkwNTE2M2dtcWo2NnR3b3dwIn0.nJ2E-tzVsVobzzYKtcKfDg'

class TripLayer extends Component{

    constructor(props) {
        super(props);
        this.state = {
            data : null ,
            tripsData: null,
            time: 26164,
            stime: Number(26165.767)
        };

    }


    componentWillUnmount() {
        if (this._animationFrame) {
            window.cancelAnimationFrame(this._animationFrame);
        }
    }
    _animate = () => {
        // const {
        //     loopLength = 1800, // unit corresponds to the timestamp in source data
        //     animationSpeed = 30 // unit time per second
        // } = this.props;
        // const timestamp = Date.now() / 1000;
        // const loopTime = loopLength / animationSpeed;
        //
        // this.setState({
        //     time: ((timestamp % loopTime) / loopTime) * loopLength
        // });
        // this._animationFrame = window.requestAnimationFrame(this._animate.bind(this));
        this.setState({
            time: this.state.time < 26250 ? this.state.time+ 0.003 : 26160
        });
        this._animationFrame = window.requestAnimationFrame(this._animate.bind(this));

    }

    componentDidMount() {
        axios.get('http://localhost:8080/geoserver/fenrir/ows?service=WFS&version=1.0.0&request=GetFeature&typeName=fenrir%3Arental_spot&outputFormat=application%2Fjson')
            .then((response) =>{
                // handle succes
                this.setState({
                    data:response.data
                })
            })
            .catch(function (error) {
                // handle error
                console.log(error);
            })
            .finally(function () {
                // always executed
            });

        axios.get('http://localhost:80/hi')
            .then( (response)=> {
//                 // handle success
                console.log(response.data);
                const tripsData = response.data.reduce((acc, cur) => {
                    const obj = acc[cur.bikeNo] || {
                        timestamps: [],
                        path:[]
                    }
                    return(
                        {
                        ...acc,
                        [cur.bikeNo] : {
                            timestamps: [
                                ...obj.timestamps,
                                Number(moment(cur.rentalDate, 'YYYYMMDDHHmm').toDate().getTime() / 60000000)
                            ],
                            path : [
                                ...obj.path,
                                [
                                    cur.rentalSpot.lon,
                                    cur.rentalSpot.lat,
                                ]
                            ]
                        }}
                    )
                }, {})
                const trips = Object.keys(tripsData).reduce((acc,cur)=>{
                    return [
                        ...acc,
                        {
                            vendor :cur,
                            ...tripsData[cur]
                        }
                    ]
                },[])
                console.log(trips)
                this.setState({
                    tripsData: trips,
                })
                this._animate()
// //
            })


    }
    renderLayer = () =>{
        if(!this.state.tripsData)
            return

        console.log(this.state.time)
        return [
            new TripsLayer({
                id: 'trips',
                data: this.state.tripsData,
                getPath: d => d.path,
                getTimestamps: d => {
                    return  d.timestamps
                },
                getColor: d => [Math.floor(Math.random()*256), Math.floor(Math.random()*256), Math.floor(Math.random()*256)], // d.vendor === 0 ? [253, 128, 93] : [23, 184, 190],
                opacity: 1,
                widthMinPixels: 5,
                rounded: true,
                trailLength: 5,
                currentTime: this.state.time,
                shadowEnabled: false
            }),
            new GeoJsonLayer({
                id:'geojson-layer',
                data:this.state.data,
                pickable: true,
                stroked: false,
                filled: true,
                extruded: true,
                lineWidthScale: 20,
                lineWidthMinPixels: 2,
                getFillColor: [160, 160, 180, 200],
                getRadius: 100,
                getLineWidth: 1,
                getElevation: 30,
            }),

        ]

    }



    render() {

        return (
            <div>
                <DeckGL
                    initialViewState={{
                        longitude: 126.983304,
                        latitude: 37.558194,
                        zoom:11,
                        pitch: 0,
                        bearing: 0
                    }}
                    layers={this.renderLayer()}
                    controller={true}>
                    <StaticMap mapboxApiAccessToken={MAPBOX_ACCESS_TOKEN} mapStyle={'mapbox://styles/mapbox/dark-v9'} />
                </DeckGL>
            </div>
        )
    }

}
export default TripLayer
